*, *::before, *::after{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
html, body, #root{
  height:100%;
}
body{
  font-size:16px;
  font-family:'Century Gothic',Helvetica;
  color:#616161;
}
div#root{
  display:grid;
  grid-template-columns:20% 20% 10% 10% 20% 20%;
}

@media only screen and(max-width:600px){
  div#root{
    grid-template-columns:10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
  }
}